<template>
  <div>
    <van-nav-bar title="支付" left-arrow @click-left="onClickleft"/>
    <van-cell-group>
      <van-cell title="订单类型" value="商品订单"/>
      <van-cell title="订单号" value="16299688438105"/>
      <van-cell title="订单金额">

        <template>
          <span class="custom-title">￥2000</span>
        </template>
      </van-cell>
    </van-cell-group>


    <van-cell-group style="margin-top: 20px;">
      <van-cell title="微信支付" label="描述信息" is-link @click="pay(1)">
        <template #icon>
          <img src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fimg.mp.itc.cn%2Fupload%2F20170401%2F6c5c86fe9c9f4b76b4fae0927fd92ad4_th.jpeg&refer=http%3A%2F%2Fimg.mp.itc.cn&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1632572158&t=71e5497572fedfa41be8cd8cfa279f33" class="pay_img">
        </template>
      </van-cell>

      <van-cell title="支付宝支付" label="描述信息" is-link>
        <template #icon>
          <img src="https://gimg2.baidu.com/image_search/src=http%3A%2F%2F5b0988e595225.cdn.sohucs.com%2Fimages%2F20200317%2F38bdf158335c4a4fa9db714c00118687.jpeg&refer=http%3A%2F%2F5b0988e595225.cdn.sohucs.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1632572206&t=0c28d9558c2f94f16630c6e2d88b25f9" class="pay_img">
        </template>
      </van-cell>

      <van-cell title="银联支付" label="描述信息" is-link>
        <template #icon>
          <img src="https://img1.baidu.com/it/u=1007726247,4277912900&fm=26&fmt=auto&gp=0.jpg" class="pay_img">
        </template>
      </van-cell>

    </van-cell-group>


  </div>
</template>

<script>
export default {
  name: "payment",
  data() {
    return {}
  },
  methods: {
    pay(id) {
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
      });
      setTimeout(
        this.$router.push('result'), 3000
      );
    },
    onClickleft() {
      this.$router.go(-1);
    }
  }

}
</script>

<style scoped>
.custom-title {
  color: #FF0000;
}

.pay_img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
}
</style>



